  .button--bordered:hover::before, .button--bordered:focus::before {
    width: 100%;
    height: 100%;
    background-color: #60a058;
  }
}
.button--bordered::before {
  content: "";
  display: block;
  width: 87%;
  height: 67%;
  border-radius: 200px;
  background-color: #467c3f;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: width 0.3s ease, height 0.3s ease, background-color 0.3s ease;
}
